import { Component, h, Prop, Element, Host } from '@stencil/core';

/**
 * @category Navigation
 * @description A breadcrumb item component for the breadcrumb.
 */

@Component({
    tag: 'at-breadcrumb-item',
    shadow: false,
})
export class AtBreadcrumbItem {
    /**
     * Applies different styling and makes the element non-clickable when set
     */
    @Prop()
    is_active?: boolean;

    /**
     * Label for the breadcrumb item
     */
    @Prop()
    label!: string;

    @Element() el;

    render() {
        return (
            <Host class="flex w-fit items-center">
                {this.is_active ? (
                    <span
                        aria-disabled="true"
                        aria-current="page"
                        class="text-active-foreground cursor-default rounded-lg px-8 py-2 capitalize"
                    >
                        {this.label}
                    </span>
                ) : (
                    <a class="text-light hover:text-dark cursor-pointer rounded-lg px-8 py-2 capitalize transition-colors hover:underline">
                        {this.label}
                    </a>
                )}
            </Host>
        );
    }
}
